import "./App.css";
import { useState, useEffect, useRef } from "react";
import firebase from "firebase/app";
import "firebase/functions";
import TextField from "./Fields/TextField";
import ListSelector from "./Fields/ListSelector";
import PrimaryButton from "./Buttons/PrimaryButton";
import { ReactComponent as Logo } from "./Logo.svg";
import Navbar from "./Navbar";
import { motion } from "framer-motion";
import lottie from "lottie-web";
import Question from "./Questions/Question";
import RegistrationSuccessful from "./RegistrationSuccessful";

const firebaseConfig = {
  apiKey: "AIzaSyCJMWn5UpHTVEKDE-gDpxVB4r91H6mUK8s",
  authDomain: "trimatch-b613b.firebaseapp.com",
  databaseURL: "https://trimatch-b613b.firebaseio.com",
  projectId: "trimatch-b613b",
  storageBucket: "trimatch-b613b.appspot.com",
  messagingSenderId: "441864222282",
  appId: "1:441864222282:web:27ef1fe7471aba8f3798b3",
  measurementId: "G-FRQPZDTHMK",
};

firebase.initializeApp(firebaseConfig);

function App() {
  const [isFilled, setIsFilled] = useState(false);
  const [isRegistered, setIsRegistered] = useState(false);
  const [isAnimated, setIsAnimated] = useState(false);
  const [currentQuestion, setCurrentQuestion] = useState(0);
  const [currentAnswer, setCurrentAnswer] = useState(null);
  const [answers, setAnswers] = useState({});
  const profiles = [
    "biol-chem-mat",
    "biol-chem",
    "mat-geo",
    "human",
    "mat-fiz",
    "mat-inf",
    "mat-pol/IB",
  ];
  const years = ["1", "2 po podstawówce", "2 po gimnazjum", "3"];
  const questions = [
    {
      type: "1",
      name: "Płeć:",
      answers: ["Kobieta", "Mężczyzna", "Osoba niebinarna"],
    },
    {
      type: "1",
      name: "Szukasz:",
      answers: [
        "Chłopaka",
        "Dziewczyny",
        "Przyjaciela",
        "Związku (płeć nie ma znaczenia)",
      ],
    },
    {
      type: "2",
      name: "Twoje zainteresowania:",
      answers: [
        "Technologia",
        "DIY",
        "Literatura",
        "Sport",
        "Motoryzacja",
        "Kinematografia",
        "Kulinaria",
        "Moda",
        "Muzyka",
        "Fotografia",
        "Podróże",
        "Gry planszowe",
      ],
    },
    {
      type: "1",
      name: "Idealne spotkanie:",
      answers: ["Kino", "Spacer", "Melanż", "Jedzenie", "Uprawianie sportu"],
    },
    {
      type: "1",
      name: "Czy jesteś wege?",
      answers: ["Tak", "Nie"],
    },
    {
      type: "1",
      name: "Ulubiona forma aktywnośći fizycznej",
      answers: [
        "Bieganie",
        "Piłka nożna",
        "Koszykówka",
        "Jazda na rowerze",
        "Spacer",
        "Pływanie",
        "Tenis",
        "Siłownia",
        "Pilates",
      ],
    },
    {
      type: "1",
      name: "Psy czy koty:",
      answers: ["Psy", "Koty"],
    },
    {
      type: "3",
      name: "Ulubiony gatunek muzyczny i artysta",
    },
    {
      type: "3",
      name: "Ulubiony film:",
    },
  ];
  const questionCount = questions.length;

  const [email, setEmail] = useState("");
  const [rocznik, setRocznik] = useState("Rocznik");
  const [profil, setProfil] = useState("Profil");
  const [name, setName] = useState("");
  const [surname, setSurname] = useState("");
  const [socialmedia, setSocialmedia] = useState("");

  function saveData() {
    setIsAnimated(true);
    setTimeout(() => {
      lottie.loadAnimation({
        container: lottieContainer.current,
        renderer: "svg",
        loop: false,
        autoplay: true,
        animationData: require("./assets/success.json"),
      });
    }, 500);

    setTimeout(() => {
      setIsRegistered(true);
    }, 2000);
  }

  const [width, setWidth] = useState(window.innerWidth);
  const breakpoint = 767;

  useEffect(() => {
    const handleWindowResize = () => setWidth(window.innerWidth);
    window.addEventListener("resize", handleWindowResize);

    return () => window.removeEventListener("resize", handleWindowResize);
  }, []);

  const lottieContainer = useRef(null);

  function submitQuestion() {
    if (questions[currentQuestion].type === "1") {
      const ans = questions[currentQuestion].answers[currentAnswer];
      setCurrentAnswer(ans);
      console.log(currentAnswer);
    }
    setAnswers({ ...answers, [currentQuestion]: currentAnswer });
    setCurrentAnswer("");
    setCurrentQuestion(currentQuestion + 1);
  }

  useEffect(() => {
    if (currentQuestion === questionCount) {
      const saveData = firebase.functions().httpsCallable("registerUser");
      saveData({
        name: name,
        surname: surname,
        profile: profil,
        year: rocznik,
        email: email,
        social: socialmedia,
        answers: answers,
      })
        .then((res) => {
          console.log(res.data);
        })
        .catch((err) => {
          console.log(err.data);
        });
    }
  }, [currentQuestion]);

  useEffect(() => {
    if (
      email != "" &&
      rocznik != "Rocznik" &&
      profil != "Profil" &&
      name != "" &&
      surname != "" &&
      socialmedia != ""
    )
      setIsFilled(true);
  }, [email, rocznik, profil, name, surname, socialmedia]);

  // useEffect(() => {
  //   if (currentAnswer !== [] && currentAnswer !== null) setIsFilled(true);
  // }, [currentAnswer]);

  return (
    <div className="App">
      {width > breakpoint ? <Logo className="big-logo" /> : <Navbar />}
      <div className="form-section">
        {isRegistered ? (
          currentQuestion < questionCount ? (
            <>
              <Question
                type={questions[currentQuestion].type}
                name={questions[currentQuestion].name}
                answers={questions[currentQuestion].answers}
                setAnswer={setCurrentAnswer}
                currentAnswer={currentAnswer}
                buttonAction={submitQuestion}
                currentQuestion={currentQuestion}
                buttonState={isFilled}
              />
              <PrimaryButton
                clickAction={submitQuestion}
                name={
                  currentQuestion === 8 ? "Podsumowanie" : "Następne pytanie"
                }
                isActive={isFilled}
              />
            </>
          ) : (
            <RegistrationSuccessful />
          )
        ) : (
          <>
            <motion.div
              className="lottie-container"
              ref={lottieContainer}
              animate={isAnimated ? { opacity: 0 } : { opacity: 1 }}
              transition={{ delay: 1.5 }}
            />
            <motion.div
              className="text-fields"
              animate={
                isAnimated ? { opacity: 0, y: -50 } : { opacity: 1, y: 0 }
              }
            >
              <div className="double-text-field">
                <TextField
                  type="text"
                  placeholder="Imię"
                  value={name}
                  setValue={setName}
                />
                <TextField
                  type="text"
                  placeholder="Nazwisko"
                  value={surname}
                  setValue={setSurname}
                />
              </div>
              <div className="double-text-field">
                <ListSelector
                  setValue={setProfil}
                  selectOptions={profiles}
                  selectName="Profil"
                  value={profil}
                />
                <ListSelector
                  setValue={setRocznik}
                  selectOptions={years}
                  selectName="Rocznik"
                  value={rocznik}
                />
              </div>
              <TextField
                type="email"
                placeholder="twój@email.com"
                value={email}
                setValue={setEmail}
                className="short-version"
              />
              <TextField
                type="text"
                placeholder="Twoje social media"
                value={socialmedia}
                setValue={setSocialmedia}
                className="short-version"
              />
              <PrimaryButton
                clickAction={saveData}
                name="Zarejestruj się"
                isActive={isFilled}
              />
            </motion.div>
          </>
        )}
      </div>
    </div>
  );
}

export default App;
